.recipes-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    grid-gap: 1.5em;
}

.recipe-card {
    border-radius: .4em;
    overflow: hidden;
    box-shadow: 0 5px 10px var(--shadow-color);

    .recipe-card-info {
        padding: 15px;
    }

    .custom-image {
        z-index: -1;
    }

    .author-img {
        height: 60px;
        width: 60px;
        border-radius: 50%;
        border: 3px solid #fff;
        box-shadow: 0 1px 3px var(--shadow-color);
        margin-top: -50px;
        z-index: 2;
    }

    .recipe-title {
        font-size: 1.25em;
        font-weight: bold;
        margin-bottom: .5em;
        color: var(--text-color);
        margin-top: .25em;
    }

    .recipe-description {
        color: var(--text-light);
        font-size: .9em;
        letter-spacing: 1px;
    }

    .view-recipe-btn {
        font-size: 1.2em;
        color: var(--primary-color);
        font-weight: bold;
        display: inline-block;
        margin-top: .5em;
    }
}